<template>
  <div class='enterpage'>
    <div class="enterclick" @click="enter">
      <p>AN HUIZHONG</p>
      <div class="line"></div>
      <div class='click-btn'>
        <a>click to enter</a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  methods: {
    enter () {
      this.$router.push('/homepage')
    }
  }
}
</script>

<style scoped>
.enterpage {
  background: #c5e7f1;
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
}
.enterclick {
  border: 1px solid red;
  padding: 20px 60px;
  cursor: pointer;
}
.enterclick p {
  text-align: center;
  margin:20px 0;
  color: #6f89a2;
  text-transform: uppercase;
  font-size: 24px;
  font-family:"Source Sans Pro", "Helvetica Neue", Arial, sans-serif;
  letter-spacing: 0.3em;
}
.click-btn {
  text-align: center;
  margin: 20px;
}
.click-btn a {
  font-size: 16px;
  text-transform: uppercase;
  font-family:"Source Sans Pro", "Helvetica Neue", Arial, sans-serif;
  color: #6f89a2;
}
.line {
  position: relative;
  left: 45%;
  height:2px;
  width: 10%;
  background: #6f89a2;
}
</style>



